<template>
  <div class="aside">
    <Menu :theme="theme2" :active-name="active" :open-names="['1']"  accordion style="margin-top: 15px;">
      <Submenu name="1">
        <template slot="title">
          <i class="iconfont icon-guanli" style="font-size: 20px;color: #6098FF;"></i>
          <span>用户管理</span>
        </template>
        <MenuItem name="1-1" @click.native="link('1-1')">
          <span>用户列表</span>
        </MenuItem>
        <MenuItem name="1-3" @click.native="link('1-3')" v-if="power === '0'">
          <span>代理商列表</span>
        </MenuItem>
        <MenuItem name="1-2" @click.native="link('1-2')">
          <span>代理权限组</span>
        </MenuItem>
         <MenuItem name="1-4" @click.native="link('1-4')">
          <span>升级套餐</span>
        </MenuItem>
      </Submenu>
      <Submenu name="2">
        <template slot="title">
           <i class="iconfont icon-xiaochengxu" style="font-size: 20px;color: #6098FF;"></i>
          <span>小程序管理</span>
        </template>
        <MenuItem name="2-1" @click.native="link('2-1')">
          <span>小程序列表</span>
        </MenuItem>
      </Submenu>
      <Submenu name="3">
        <template slot="title">
           <i class="iconfont icon-yonghu" style="font-size: 24px;color: #6098FF;"></i>
          <span>终端用户</span>
        </template>
          <MenuItem name="3-1" @click.native="link('3-1')">
            <span>用户列表</span>
          </MenuItem>
      </Submenu>
      <MenuItem name="4-1" @click.native="link('4-1')">
        <i class="iconfont icon-shezhi" style="font-size: 24px;color: #6098FF;"></i>
        <span>设置</span>
      </MenuItem>
    </Menu>
    </Col>
  </Row>
  </div>
</template>
<script>
  export default {
    data () {
      return {
        theme2: 'light',
        active:"1-1",
        power:''
      }
    },
    methods:{
      link(val){
        switch(val){
          case '1-1':
            this.$router.push('/userList')
            break;
          case '1-2':
            this.$router.push('/userPower')
            break;
          case '1-3':
            this.$router.push('/agentList')
            break;
           case '1-4':
            this.$router.push('/upgrade')
            break;
          case '2-1':
            this.$router.push('/appList')
            break;
          case '3-1':
            this.$router.push('/terminal')
            break;
          case '4-1':
            this.$router.push('/set')
            break;
        }
      }
    },
    created(){
      let name = this.$route.name;
      switch(name) {
        case 'userList':
          this.active='1-1'
          break;
        case 'userPower':
          this.active='1-2'
          break;
        case 'agentList':
          this.active='1-3'
          break;
        case 'upgrade':
          this.active='1-4'
          break;
        case 'appList':
          this.active='2-1'
          break;
        case 'terminal':
          this.active='3-1'
          break;
        case 'set':
          this.active='4-1'
          break;
         
      }
    },
    mounted () {
     this.power = sessionStorage.getItem('user');
    }
  }
</script>
<style>
.aside{
  flex:1;
  background: #0E1A35;
}
.ivu-menu-light{
  background: none !important;
  border-right: 1px solid #0E1A35 !important;
}
  .ivu-icon {
    color: #6098FF !important;
  }
  .ivu-menu-vertical .ivu-menu-item, .ivu-menu-vertical .ivu-menu-submenu-title{
    color: white !important;
    height: 64px !important;
    line-height: 36px !important;
  }
  .ivu-menu-vertical .ivu-menu-item:hover, .ivu-menu-vertical .ivu-menu-submenu-title:hover{
    background: #122143 !important;
  }
  .ivu-menu-light.ivu-menu-vertical .ivu-menu-item-active:not(.ivu-menu-submenu){
    background: ;
    border-left: 6px solid #5584FF;
    border-right: none !important;
  }
  .ivu-menu-submenu-title-icon{
    display: none !important;
  }
  .ivu-menu-vertical .ivu-menu-submenu .ivu-menu-item{
    text-align: left !important;
    padding-left: 64px !important;
  }
  .ivu-menu-submenu-title span>i, .ivu-menu-submenu-title>i,.ivu-menu-item>i{
    width: 15px !important;
  }
  .ivu-menu-vertical.ivu-menu-light:after{
    background: none !important;
  }
</style>



