<template>
  <div class="topnav">
    <div class="topnav-logo">
      <img src="../assets/logo.png" alt="">
    </div>
    <div class="top_msg">
    </div>
    <div class="topnav-right">
      <ul>
        <li class="topLi" v-for="(value,key,index) in logIn" :class="index == 2 ? costHover :''">
          <a v-if="index == 0" style="text-align: center;margin-top: 1rem">
            <!--<img src="../../assets/dis.jpg" alt="" style="width: 3rem;height: 3rem;border-radius:3rem ">-->
          </a>
          <a v-if="index == 1" style="font-size: 16px;">
            {{value}}
          </a>
          <a v-if="index==2" style="margin-right: 50px;margin-top: 5px;">
            <!--<img src="../../assets/accoun.png" alt="">-->
            <Icon type="android-person" size="25"></Icon>
            <Icon type="arrow-down-b" style="margin-left: 10px;"></Icon>
          </a>
          <div v-if="index == 1" class="user">
          </div>
          <div v-if="index == 2" class="cost">
            <div class="cost-top">
              <span class="balance">账户余额</span>
              <span class="recharge" @click="charge">立即充值</span>
            </div>
            <div class="money">
              <h2 style="font-size: 35px;display: inline-block;">{{fee || 0}}</h2><sub>元</sub>
            </div>
            <div class="cost-my">
              <ul>
                <li><a @click="seeInfo">个人中心</a></li>
                <li><a @click="set">设置</a></li>
                <li>
                  <a @click="exit" class="quit">退出</a>
                </li>
              </ul>
            </div>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        fee:'',
        logIn:{
          headPortrait:"头像",
          name:"西瓜科技",
          cost:"账户",
        },
        logState:true,
        userHover:"user-hover",
        costHover:"cost-hover"
      }
    },
    props:['navData'],
    methods: {
      charge(){

      },
      seeInfo(){

      },
      set(){
        //设置

      },
      exit(){
        this.$Modal.confirm({
          title: '退出',
          content: '<p>确定退出系统吗</p>',
          onOk: () => {
              this.$router.push('/');
          },
          onCancel: () => {
//            this.$Message.info('点击了取消');
          }
        });
      },
    },
  }
</script>

<style scoped>
  .ivu-icon{
    color: black !important;
  }
  .topnav{
    background-color: #FFFFFF;
    width: 100%;
    height: 60px;
    display: flex;
    justify-content:space-around;
    border-bottom:1px solid #D7DCE5 ;
    align-items: center;
  }
  .topLi{
    line-height: 60px;
    margin-left:-15px;
    vertical-align: top;
    display: inline-block;
    position: relative;
  }
  .topLi :hover{
    color: #2277da;
  }
  .topnav-logo{
    height: 60px;
    line-height: 60px;
    flex:0 0 240px;
    padding-left: 20px;
    padding-top: 5px;
    background: #0E1A35;
  }
  .topnav-logo>img{
    width: 150px;
  }
  .top_msg{
    flex-basis: 225px;
    display: flex;
    padding: 0 15px;
    align-items: center;
  }
  .top_msg p{
    text-align: center;
    color:white;
  }
  .user-hover:hover .user{
    /*display: inline-block;*/
  }
  .cost-hover:hover .cost{
    display: inline-block;
  }
  .topLi>a{
    display: inline-block;
    padding: 0 15px;
    width:100%;
    height:100%;
    text-align: center;
    font-weight: 550;
    transition-property: all;
    transition-duration: .2s;
    transition-timing-function: initial;
  }
  .topLi>li:nth-child(3){
    margin-right: 20px;
  }
  .topnav-right{
    width:200px;
    height:60px;
    line-height: 100px;
    text-align:right;
    padding: 0 60px 0 0;
    flex:1;
  }
  .topnav-right>ul>.topLi{
    /*background: url(../../static/img/nav.png) no-repeat;*/
    background-position: right center;
    font-size: 14px;
  }
  .topnav-right>ul li:last-child{
    background-image: none;
  }
  .user{
    display: none;
    position: absolute;
    top:60px;
    right:0;
    width:140px;
    z-index: 1000;
    padding:10px 20px;
    background-color: #262626;
  }
  .user>ul>li>a{
    padding:0;
  }
  .user>ul>li{
    height:32px;
    line-height:35px;
    width:100px;
  }
  .user>ul li:last-child{
    margin-top:10px;
    height:35px;
    line-height: 40px;
    border-top:.5px solid #515151;
    display: block;
  }
  .cost{
    display: none;
    position: absolute;
    top:60px;
    right:30px;
    z-index: 1000;
    line-height:45px;
    width:280px;
    height:280px;
    color: white;
    background-color: #262626;
    padding:0 20px;
    text-align: left;
  }
  .balance{
    color:#999;
    -moz-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    -khtml-user-select: none;
    user-select: none;
    cursor: default;
  }
  .balance:hover{
    color:#bbbbbb;
  }
  .money{
    margin-bottom: 20px;
    -moz-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    -khtml-user-select: none;
    user-select: none;
    cursor: default;
  }
  .money,
  .money:hover >h2,
  .money:hover >sub{
    color:white;
  }
  .recharge{
    color: #ff9d00;
    float: right;
  }
  .recharge:hover {
    color:#f28f00;
  }
  .cost-top{
    width:100%;
    line-height: 50px;
    height:40px;
    vertical-align: top;
    display: inline-block;
    position: relative;
  }
  .cost-top>span{
    height:40px;
  }
  .cost a{
    color: #eeeeee;
    display: inline-block;
    height:100%;
    vertical-align: top;
    transition-property: all;
    transition-duration: .2s;
    transition-timing-function: initial;
  }
  .cost-my a , .cost-favorable a{
    color:#bbbbbb;
  }
  .cost-favorable > a {
    width:30%;
    height:30px;
    line-height: 30px;
    background-position: right center;
    color: white;
  }
  .cost-favorable a:last-child{
    background: none;
  }
  .cost-my{
    margin-top:10px;
    line-height: 30px;
  }
  .cost-my li{
    height:35px;
    line-height:35px;
  }
  .cost-my li:last-child{
    border-top:1px solid #515151;
    margin-top:10px;
    line-height:45px;
  }
  .top-recommend > a{
    width:100px;
    margin:0 10px;
    height:35px;
    padding:0;
  }
  .quit{
    margin-top: 15px;
    /*color: #6098FF !important;*/
   font-size: 16px;
  }
</style>



